import React, { useState, useEffect } from 'react';
import {
  TextField,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  IconButton,
  Typography,
  Grid,
  Paper,
  Container,
} from '@mui/material';
import { Search as SearchIcon, ArrowForward as ArrowForwardIcon } from '@mui/icons-material';
import InfiniteScroll from 'react-infinite-scroll-component';

const Weather = () => {
  const [searchText, setSearchText] = useState('');
  const [data, setData] = useState([]);
  const [hasMore, setHasMore] = useState(true);
  const [currentPage, setCurrentPage] = useState(0);

  const handleSearchChange = (event) => {
    setSearchText(event.target.value);
  };

  const fetchInitialData = async () => {
    try {
      const response = await fetch('/all'); // Fetch initial data from "/all" endpoint
      const initialData = await response.json();
      setData(initialData.slice(0, 10)); // Set the first 10 items
      setCurrentPage(1);
      if (initialData.length <= 10) {
        setHasMore(false);
      }
    } catch (error) {
      console.error('Error fetching initial data:', error);
    }
  };

  const loadMoreData = () => {
    const startIdx = currentPage * 10;
    const endIdx = startIdx + 10;

    if (endIdx < data.length) {
      setData((prevData) => [...prevData, ...data.slice(startIdx, endIdx)]);
      setCurrentPage(currentPage + 1);
    } else {
      setData((prevData) => [...prevData, ...data.slice(startIdx)]);
      setHasMore(false);
    }
  };

  useEffect(() => {
    fetchInitialData(); // Fetch initial data when component mounts
  }, []);

  const filteredData = data.filter((item) =>
    item.cityName.toLowerCase().startsWith(searchText.toLowerCase())
  );

  return (
    <Container maxWidth="md">
      <Grid container direction="column" alignItems="center" spacing={2}>
        <Grid item>
          <Typography variant="h4" align="center">
            Weather App
          </Typography>
        </Grid>
        <Grid item xs={12}>
          <TextField
            fullWidth
            variant="outlined"
            label="Search City"
            value={searchText}
            onChange={handleSearchChange}
            InputProps={{
              endAdornment: (
                <IconButton>
                  <SearchIcon />
                </IconButton>
              ),
            }}
          />
        </Grid>
        <Grid item xs={12}>
          <List>
            <ListItem component={Paper} elevation={3}>
              <ListItemText primary="City" secondary="Temperature" style={{ marginRight: '32px' }} />
              <ListItemText secondary="Wind Speed" style={{ marginRight: '32px' }} />
              <ListItemText secondary="Wind Direction" />
            </ListItem>
            <InfiniteScroll
              dataLength={filteredData.length}
              next={loadMoreData}
              hasMore={hasMore}
              loader={<h4>Loading...</h4>}
              endMessage={<p>No more data to load.</p>}
            >
              {filteredData.map((item, index) => (
                <ListItem key={index} component={Paper} elevation={3}>
                  <ListItemText
                    primary={item.cityName}
                    secondary={`${item.temperature}`}
                    style={{ marginRight: '32px' }}
                  />
                  <ListItemText secondary={`${item.windSpeedMs}`} style={{ marginRight: '32px' }} />
                  <ListItemText secondary={`${item.windDirection}`} />
                  <ListItemSecondaryAction>
                    <IconButton>
                      <ArrowForwardIcon style={{ transform: `rotate(${item.windDirection}deg)` }} />
                    </IconButton>
                  </ListItemSecondaryAction>
                </ListItem>
              ))}
            </InfiniteScroll>
          </List>
        </Grid>
        <Grid item>
          <Typography variant="body2" align="center">
             2023 Weather App
          </Typography>
        </Grid>
      </Grid>
    </Container>
  );
};

export default Weather;
